.newUser {
    flex: 4;
  }

.required-field::after {
    content: "*";
    color: red;
}
  
  .newUserForm {
    display: flex;
    flex-wrap: wrap;
  }
  
  .newUserItem {
    width: 400px;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    margin-right: 20px;
  }
  
  #password{
    width: 87%;
  }
  #loginpassword{
  
    width: 100%;
  }
  #email{
    width: 87%;
    text-align: 0%;
  }
  .newUserItem > label {
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 600;
    color: rgb(151, 150, 150);
  }
  
  .newUserItem > input {
    height: 54px;
    padding: 13px;
    width: auto;
    border: 1px solid gray;
    border-radius: 5px;
  }
  .newUserItemFile > input {
    height: 31px;
    padding: 13px;
    border-radius: 5px;
  }
  .newUserTextArea > label {
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 600;
    color: rgb(151, 150, 150);
  }
  .newUserTextArea > input{
    height: 54px;
    padding: 13px;
    width: auto;
    border: 1px solid gray;
    border-radius: 5px;
  }
  .newUserGender > input {
    margin-top: 15px;
  }
  
  .newUserGender>label{
      margin: 10px;
      font-size: 18px;
      color: #555;
  }
  
  .newUserSelect{
      height: 40px;
      border-radius: 5px;
  }
  
  .newUserButton{
      width: 200px;
      border: none;
      background-color: darkblue;
      color: white;
      padding: 7px 10px;
      font-weight: 600;
      border-radius: 10px;
      margin-top: 30px;
      cursor: pointer;
  }