.Dashboard {
    flex: 1;
    height: calc(100vh - 50px);
    background-color: rgb(251, 251, 255); 
    position: sticky;
    top: 50px;
  }
  
  .DashboardWrapper {
    padding: 20px;
    color: #555;
  }
  
  .DashboardMenu {
    margin-bottom: 5px;
  }
  
  .DashboardTitle {
    font-size: 18px;
    font-weight: bold;
    color: rgb(70, 69, 69);
  }
  
  .DashboardList {
    list-style: none;
    padding: 5px;
  }
  
  .DashboardListItem {
    padding: 6px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
  }
  
  .DashboardListItem.active,
  .DashboardListItem:hover {
    width: 100px;
    background-color: rgb(240, 240, 255);
    margin:1px
  }
  
  .DashboardIcon{
      margin-right: 5px;
      font-size: 20px !important;
  }
  