.social-container {
  background: #eee;
  padding: 25px 50px;
  /* width: 50%;
    margin: 0 auto; */
  width: 100%;
  text-align: center;

  /* justify-content: center; */
}
a.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}
a.social:hover {
  transform: translateY(-2px);
}
a.youtube {
  color: #eb3223;
}

a.tiktok {
  color: black;
}

a.facebook {
  color: #4968ad;
}

a.twitter {
  color: #49a1eb;
}

a.instagram {
  color: #bc2a8d;
}
